export const sourceCode = "# 日替わり表の印刷手順\r\n\r\n毎朝 8:30 までに、その日の製造計画となる「日替わり表」を印刷し、製造スタッフへ配布します。\r\n\r\n## 作業手順\r\n\r\n### 1. ファイルを開く\r\n\r\n以下のパスにある `CB商品データ.xlsm` を開きます。\r\n\r\n```text filename=\"ファイルパス\"\r\n\\\\Server\\Common\\01_Daily\\CB商品データ.xlsm\r\n```\r\n\r\n### 2. シートの選択と日付変更\r\n\r\n1.  Excel 下部のタブから **「夜間用」** シートを選択します。\r\n2.  セル `A1` の日付を **翌日の日付** に変更します。\r\n\r\n### 3. 印刷実行\r\n\r\n画面右上にある **「一括印刷」** ボタンをクリックします。\r\n自動的にプリンターから帳票が出力されます。\r\n\r\n> **注意:** 「マクロが無効です」という警告が出た場合は、画面上部の「コンテンツの有効化」をクリックしてください。\r\n\r\n---\r\n\r\n## チェックリスト\r\n\r\n- [ ] 日付は翌日になっていますか？\r\n- [ ] エラーメッセージは出ていませんか？";
